(function() {
  'use strict';

  angular
    .module('real-impact')
    .controller('HomeController', HomeController);

  /** @ngInject */
  function HomeController($log, $timeout, HistoricFlyFactory, $mdDialog, GraphBuilderFactory, $mdToast, $anchorScroll, $location) {
    var vm = this;

    vm.airports = [];
    vm.units = 'minutes';
    vm.per = 'hours';
    vm.chartVisible = true;

    vm.querySearch = querySearch;
    vm.getAnalythics = getAnalythics;
    vm.changeUnits = changeUnits;
    vm.changePer = changePer;
    vm.changeScroll = changeScroll;

    initController();

    function initController() {
      HistoricFlyFactory.getAirPorts().then(function(airtports){
        vm.airports = airtports;
      });

      HistoricFlyFactory.getDistanceDelays('01').then(function(result){
        vm.correlationData = result;
        paintGraph('lineChart');
      });

    }

    function querySearch(query) {
      return query
              ? vm.airports.filter(function(element) {
                return (element.name.toLowerCase().search(query.toLowerCase()) > -1 || element.id.toLowerCase().search(query.toLowerCase()) > -1);
              })
              : vm.airports;
    }

    function getAnalythics(){
      if(vm.fromItem && vm.fromItem.id && vm.toItem && vm.toItem.id){
        //We sent the month, emulating user input
        HistoricFlyFactory.getDelaysOnMonthPer('01', vm.per,vm.fromItem.id, vm.toItem.id, vm.units).then(function(result){

          if(result.length === 0){
            vm.chartVisible = false;
            showAlertDialog('Alert', 'Not Data Found');
          }else{
            vm.chartVisible = true;
          }

          HistoricFlyFactory.getDistanceFromTo('01',vm.fromItem.id, vm.toItem.id).then(function(distance){
            showToast('Current Selection Distance: '+distance+'mi');
          });

          vm.currentList = result;
          vm.showOpts = {
            month: getMonth('01'),
            units: vm.units,
            per: vm.per
          }
          paintGraph('discreteBarChart');
        });

      }else{
        showAlertDialog('Error', 'Please select a From and a To Airports');
      }
    }

    function changeUnits() {

      if(vm.currentList){
        if(vm.units === 'minutes'){
          vm.currentList = vm.currentList.map(function (e) {
            e.value = e.delays;
            return e;
          });
        }else{
          vm.currentList = vm.currentList.map(function (e) {
            e.value = e.ratios;
            return e;
          });
        }
        vm.showOpts.units = vm.units;
        paintGraph('discreteBarChart');
      }
    }

    function changePer(){
      getAnalythics();
    }

    function paintGraph(type) {
      if(type === 'discreteBarChart'){
        var graphBuilded = GraphBuilderFactory.discreteBarChart(vm.currentList, vm.showOpts);
        vm.options = graphBuilded.options;
        vm.data = graphBuilded.data;
      }

      if(type === 'lineChart'){
        var graphBuilded = GraphBuilderFactory.lineChart(vm.correlationData);
        vm.options2 = graphBuilded.options;
        vm.data2 = graphBuilded.data;
      }

    }

    function getMonth(month){
      var months = [];
      months['01'] = 'January';
      months['02'] = 'February';
      months['03'] = 'March';
      months['04'] = 'April';
      months['05'] = 'May';
      months['06'] = 'June';
      months['07'] = 'July';
      months['08'] = 'August';
      months['09'] = 'September';
      months['10'] = 'October';
      months['11'] = 'November';
      months['12'] = 'December';

      return months[month];
    }

    function showAlertDialog(title, text){
      $mdDialog.show(
      $mdDialog.alert()
          .parent(angular.element(document.querySelector('.site')))
          .clickOutsideToClose(true)
          .title(title)
          .textContent(text)
          .ariaLabel('Alert Dialog')
          .ok('Got it!')
      );
    }

    function showToast(message) {
      $mdToast.show(
        $mdToast.simple()
          .textContent(message)
          .position('top right')
          .hideDelay(6000)
          .highlightClass('md-primary')
          .action('CLOSE')
      );

    };

    function changeScroll(){
      $timeout(function(){
        $location.hash('crr');
        $anchorScroll();
      },200);
    }

  }
})();

(function() {
  'use strict';

  angular
      .module('real-impact')
      .factory('HistoricFlyFactory', HistoricFlyFactory);

  /** @ngInject */
  function HistoricFlyFactory($http, $q) {

    var self = this;
    self.data = null;
    self.dataAirports = null;
    self.airports = null;

    return {
          initData: initData,
          initDataAirports: initDataAirports,
          getAirPorts: getAirPorts,
          getDelaysOnMonthPer: getDelaysOnMonthPer,
          getDistanceDelays: getDistanceDelays,
          getDistanceFromTo: getDistanceFromTo
    };

    function initData(){
      return $http({
          method: 'GET',
          url: 'assets/data/historic-data.json'
        }).then(function(result){
          self.data = result.data;
        }, function (err) {
          self.data = null;
        });
    }

    function initDataAirports() {
      return $http({
          method: 'GET',
          url: 'assets/data/airports.json'
        }).then(function(result){
          self.dataAirports = result.data;
        }, function (err) {
          self.dataAirports = null;
        });
    }

    function getAirPorts() {

      var deferred = $q.defer(), currentCodes = [];

      self.airports = [];

      self.data.forEach(function(obj, i) {
        if (obj.hasOwnProperty('DEST') && currentCodes.indexOf(obj['DEST']) === -1) {
          currentCodes.push(obj['DEST']);
          self.airports.push({
            id: obj['DEST'],
            name: getNameByCode(obj['DEST']).name
          });
        }
        if (obj.hasOwnProperty('ORIGIN') && currentCodes.indexOf(obj['ORIGIN']) === -1) {
          currentCodes.push(obj['ORIGIN']);
          self.airports.push({
            id: obj['ORIGIN'],
            name: getNameByCode(obj['ORIGIN']).name
          });
        }

        if(i === self.data.length - 1 ){
          deferred.resolve( self.airports );
        }
      });

      return deferred.promise;

    }

    function getNameByCode(code){
      return self.dataAirports.find(function(element) {
        return element.code === code;
      })
    }

    function getDelaysOnMonthPer(month, per, from, to, units) {
      var deferred = $q.defer(), matches = {};

      var key, currentMonth;

      getDataFromTo(from, to, month).then(function(dataFromTo) {

        if(dataFromTo.length > 0){

          if(per === 'days'){
            groupByDays(dataFromTo).then(function (groupedData) {
              var values = [], delays, ratios;
              for (var day in groupedData) {
                delays = getAvg(groupedData[day].delays);
                ratios = getAvg(groupedData[day].ratios);
                values.push({
                  label: day.split("-")[2],
                  delays: delays,
                  ratios: ratios,
                  value: ((units === 'minutes')?delays:ratios)
                });
              };
              deferred.resolve(values);
            });
          }else{
            groupByHours(dataFromTo).then(function (groupedData) {
              var values = [], delays, ratios, label;
              for (var hour in groupedData) {
                delays = getAvg(groupedData[hour].delays);
                ratios = getAvg(groupedData[hour].ratios);
                label = hour.toString();
                label = (label.length === 3)?('0'+label):label;
                label = label.slice(0, 2) + ":" + label.slice(2);
                if(label.length == 5)
                  values.push({
                    label: label,
                    delays: delays,
                    ratios: ratios,
                    value: ((units === 'minutes')?delays:ratios)
                  });
              };
              deferred.resolve(values);
            });
          }

        }else{
          deferred.resolve([]);
        }

      });

      return deferred.promise;

    }

    function getDataFromTo(from, to, month) {
      var deferred = $q.defer(), result = [];
      self.data.forEach(function(obj, i) {
        if(obj['ORIGIN'] === from && obj['DEST'] === to && obj['FL_DATE'].split("-")[1] === month){
          result.push(obj);
        }

        if(i === self.data.length - 1 ){
          deferred.resolve(result);
        }

      });
      return deferred.promise;
    }

    function groupByDays(list) {
      var deferred = $q.defer();
      var key, group = {};

      list.forEach(function (element, i) {
        key = element['FL_DATE'];
        if(!group[key]){
          group[key] = { delays: [], ratios: [] };
        }

        group[key].delays.push(element['ARR_DELAY']);
        group[key].ratios.push((element['ARR_DELAY']/element['CRS_ELAPSED_TIME']) * 100);

        if(i === list.length - 1 ){
          deferred.resolve(group);
        }

      });

      return deferred.promise;
    }

    function groupByHours(list) {
      var deferred = $q.defer();
      var key, group = {};

      list.forEach(function (element, i) {
        key = element['CRS_DEP_TIME'];
        if(!group[key]){
          group[key] = { delays: [], ratios: [] };
        }

        group[key].delays.push(element['ARR_DELAY']);
        group[key].ratios.push((element['ARR_DELAY']/element['CRS_ELAPSED_TIME']) * 100);

        if(i === list.length - 1 ){
          deferred.resolve(group);
        }

      });

      return deferred.promise;
    }

    function getDistanceDelays(month, units) {
      var deferred = $q.defer(), distance ,distances = [];
      self.data.find(function(obj, i) {

        distance = obj['DISTANCE'];

        if(obj['FL_DATE'].split("-")[1] === month){
          if(!distances[distance]){
            distances[distance] = { delays: [] };
          }
          distances[distance].delays.push(obj['ARR_DELAY']);
        }

        if(i === self.data.length - 1 ){
          var values = [], delays, toAnalyze = [];
          for (var distance in distances) {
            delays = getAvg(distances[distance].delays);
            values.push(new DataPoint(parseInt(distance), delays));
            toAnalyze.push({ x: parseInt(distance)});
          };

          var poly = new PolynomialRegression(values, 40);
          var terms = poly.getTerms();
          var regressionData = [];

          toAnalyze.forEach(function(row, j){
            regressionData.push({x: row.x, y: poly.predictY(terms, row.x)});
            if(j === toAnalyze.length - 1 ){
              deferred.resolve(regressionData);
            }
          });
        }
      });

      return deferred.promise;
    }

    function getDistanceFromTo(month, from, to) {
      var deferred = $q.defer(), distance = null;
      self.data.forEach(function(obj, i) {
        if(obj['ORIGIN'] === from && obj['DEST'] === to && obj['FL_DATE'].split("-")[1] === month){
          distance = obj['DISTANCE'];
          deferred.resolve(distance);
          return distance;
        }
      });
      return deferred.promise;
    }

    function getAvg(array) {
      var sum = 0;
      var total = 0;

      for( var i = 0; i < array.length; i++ ){
        if(array[i] !== ""){
          total++;
          sum += array[i];
        }
      }
      return total > 0 ? sum/total : 0;
    }

  }

})();

(function() {
  'use strict';

  angular
      .module('real-impact')
      .factory('GraphBuilderFactory', GraphBuilderFactory);

  /** @ngInject */
  function GraphBuilderFactory() {

    var self = this;

    self.height = 450;
    self.margin = {
        top: 40,
        right: 20,
        bottom: 70,
        left: 70
    };
    self.maxDelayMinutes = 20;
    self.midDelayMinutes = 10;
    self.maxDelayPercent = 15;
    self.midDelayPercent = 8;
    self.red = '#f44336';
    self.yellow = '#ffeb3b';
    self.green = '#4caf50';
    self.delay = 200;

    return {
      discreteBarChart: discreteBarChart,
      lineChart: lineChart
    };

    function discreteBarChart(values, opts) {

      var options = {
        chart: {
            type: 'discreteBarChart',
            height: self.height,
            margin : self.margin,
            x: function(d){ return d.label; },
            y: function(d){ return d.value; },
            showValues: true,
            valueFormat: function(d){
                return d3.format(',.0f')(d)+((opts.units === 'ratio')?'%':'');
            },
            color: function (d, i) {
                var max = ((opts.units === 'minutes')?self.maxDelayMinutes:self.maxDelayPercent);
                var min = ((opts.units === 'minutes')?self.midDelayMinutes:self.midDelayPercent);
                if (d.value > max){
                  return self.red;
                }else if(d.value > min){
                  return self.yellow;
                }else{
                  return self.green;
                }
            },
            duration: self.delay,
            xAxis: {
                axisLabel: ((opts.per === 'days')?'Departure Day':'Departure Hour'),
                axisLabelDistance: 10,
                tickFormat:function(d, i){
                  if(opts.per === 'hours'){
                    return ((i % 3 === 0)?d:'');
                  }else{
                    return d;
                  }
                },
            },
            yAxis: {
                axisLabel: 'Arrival Delay '+((opts.units === 'minutes')?'(Minutes)':'Ratio %'),
                axisLabelDistance: 0,
                tickFormat:function(d, i){
                  return d3.format(',.0f')(d) + ((opts.units === 'ratio')?'%':'');
                }
            },
            tooltip:{
              classes: 'graph-tooltip md-whiteframe-z1',
              valueFormatter: function (d, i) {
                return d3.format(',.0f')(d)+(((opts.units === 'minutes')?' Minutes':'%')+' of arrival delay');
              },
              keyFormatter:function (d, i) {
                return 'on '+opts.month+' '+d;
              }
            }
        },
        title:{
          enable: true,
          text: "Flights arrival delay per departure "+((opts.per === 'days')?'day':'hour')+" on "+opts.month,
          className: "graph-title"
        },
        subtitle:{
          enable: true,
          text: "Higher columns means more delay",
          className: "graph-subtitle"
        }
      };

      var data = [
        {
            key: '01',
            values: values
        }
      ];

      return {
        options: options,
        data: data
      };

    }

    function lineChart(values) {

      var options = {
          chart: {
              type: 'lineChart',
              height: self.height,
              margin : self.margin,
              x: function(d){ return d.x; },
              y: function(d){ return d.y; },
              useInteractiveGuideline: true,
              showLegend: false,
              xAxis: {
                  axisLabel: 'Distance of Flight'
              },
              yAxis: {
                  axisLabel: 'Delay in Minutes',
                  tickFormat: function(d){
                      return d3.format('.0f')(d);
                  },
                  axisLabelDistance: -10
              }
          },
          title:{
            enable: true,
            text: "Correlation Between Arrival Delay and Distance",
            className: "graph-title"
          },
          subtitle:{
            enable: true,
            text: "Calculed using a Polynomial Regression",
            className: "graph-subtitle"
          }
      };

      var data = [
        {
          values: values,
          key: 'Delay in Minutes: ',
          color: 'rgb(255,152,0)',
          strokeWidth: 2,
        }
      ]

      return {
        options: options,
        data: data
      };

    }


  }

})();


/**
 * constructs a Correlation object with a few public methods for analyzing data sets
 * @param x - an array of Numbers
 * @param y - an array of Numbers
 * @constructor
 */
function Correlation(x, y){
    this.x       = x;
    this.y       = y;
}

/**
 * Gets the correlation coefficient of 2 lists
 * @returns {number}
 */
Correlation.prototype.correlationCoefficient = function(){
    return this.diffFromAvg() / (Math.sqrt(this.diffFromAvgSqrd(X) * this.diffFromAvgSqrd(Y)));
};


/**
 * get the average of a list
 * @param aList - array
 * @returns {number}
 */
Correlation.prototype.avg = function(aList){
    var sum = 0;
    for(var i = 0; i < aList.length; i++){
        sum += aList[i];
    }
    return sum / aList.length;
};

/**
 * gets the standard deviation of an array
 * @param aList
 * @returns {number}
 */
Correlation.prototype.stdv = function(aList){
    return Math.sqrt(this.diffFromAvgSqrd(aList) / (aList.length - 1));
};

/**
 * The B part of the regression equation -> y = mx + B
 * @returns {number}
 */
Correlation.prototype.b0 = function(){
    return this.avg(this.y) - this.b1() * this.avg(this.x);
};

/**
 * the M part of the regression equation -> y = Mx + b
 * @returns {number}
 */
Correlation.prototype.b1 = function(){
    return this.diffFromAvg() / this.diffFromAvgSqrd(X);
};


/**
 * gets the sum of (Xi - Mx)(Yi - My)
 * @returns {number}
 */
Correlation.prototype.diffFromAvg = function(){
    var sum = 0;
    for(var i = 0; i < this.x.length; i++){
        sum += (this.x[i] - this.avg(x)) * (y[i] - this.avg(y));
    }
    return sum;
};

/**
 * Returns the sum of (Xi - Mx)^2
 * @param aList
 * @returns {number}
 */
Correlation.prototype.diffFromAvgSqrd = function(aList){
    var sum = 0;
    for(var i = 0; i < aList.length; i++){
        sum += Math.pow((aList[i] - this.avg(aList)), 2);
    }
    return sum;
};

/**
 * Gets the sum of a list
 * @param aList
 * @returns {number}
 */
Correlation.prototype.sumList = function(aList){
    var sum = 0;

    for(var i = 0; i < aList.length; i++){
        sum += aList[i];
    }
    return sum;
};

/**
 * sum of each list item squared
 * @param aList
 * @returns {number}
 */
Correlation.prototype.sumSquares = function(aList){
    var sum = 0;

    for(var i = 0; i < aList.length; i++){
        sum += Math.pow(aList[i], 2);
    }
    return sum;
};

Correlation.prototype.sumXTimesY = function(){
    var sum = 0;

    for(var i = 0; i < this.x.length; i++){
        sum += (this.y[i] * this.x[i]);
    }
    return sum;
};

/**
 * Gives the predicted value of the dependent variable based on the independent variable.
 * The equation is in the from y = mx + b
 * @param independentVariable
 * @returns {number}
 */
Correlation.prototype.linearRegression = function(independentVariable){
    return this.b1() * independentVariable + this.b0();
};

function Matrix(){

}

/**
 * performs backward substitution on a matrix
 * @param anyMatrix - a matrix that has already undergone forward substitution
 * @param arr - an array that will ultimately be the final output for A0 - Ak
 * @param row - last row index
 * @param col - column index
 * @returns {*}
 */
Matrix.prototype.backwardSubstitution = function(anyMatrix, arr, row, col){

    if(row < 0 || col < 0){
        return arr;
    } else{
        var rows = anyMatrix.length;
        var cols = anyMatrix[0].length - 1;
        var current = 0;
        var counter = 0;

        for(var i = cols - 1; i >= col; i--){

            if(i == col){
                current = anyMatrix[row][cols] / anyMatrix[row][i];


            } else{
                anyMatrix[row][cols] -= anyMatrix[row][i] * arr[rows - 1 - counter];
                counter++;
            }
        }

        arr[row] = current;
        return this.backwardSubstitution(anyMatrix, arr, row - 1, col - 1);
    }
};


/**
 * Combines a square matrix with a matrix with K rows and only 1 column for GJ Elimination
 * @param left
 * @param right
 * @returns {*[]}
 */
Matrix.prototype.combineMatrices = function(left, right){

    var rows = right.length;
    var cols = left[0].length;
    var returnMatrix = [];

    for (var i = 0; i < rows; i++) {
        returnMatrix.push([]);

        for (var j = 0; j <= cols; j++) {

            if (j == cols) {

                returnMatrix[i][j] = right[i];

            } else {

                returnMatrix[i][j] = left[i][j];
            }
        }
    }

    return returnMatrix;
};

/**
 * Performs forward elimination for GJ elimination to form an upper right triangle matrix
 * @param anyMatrix
 * @returns {*[]}
 */
Matrix.prototype.forwardElimination = function(anyMatrix){

    var rows        = anyMatrix.length;
    var cols        = anyMatrix[0].length;
    var aMatrix     = [];
    //returnMatrix = anyMatrix;
    for (var i = 0; i < rows; i++) {

        aMatrix.push([]);

        for (var j = 0; j < cols; j++) {
            aMatrix[i][j] = anyMatrix[i][j];
        }
    }

    for (var x = 0; x < rows - 1; x++) {

        for (var z = x; z < rows - 1; z++) {

            var numerator   = aMatrix[z + 1][x];
            var denominator = aMatrix[x][x];
            var result      = numerator / denominator;


            for (var i = 0; i < cols; i++) {

                aMatrix[z + 1][i] = aMatrix[z + 1][i] - (result * aMatrix[x][i]);
            }
        }
    }
    return aMatrix;
};

/**
 * THIS METHOD ACTS LIKE A CONTROLLER AND PERFORMS ALL THE NECESSARY STEPS OF GJ ELIMINATION TO PRODUCE
 * THE TERMS NECESSARY FOR POLYNOMIAL REGRESSION USING THE LEAST SQUARES METHOD WHERE SUM(RESIDUALS) = 0
 * @param leftMatrix
 * @param rightMatrix
 * @returns {*}
 */
Matrix.prototype.gaussianJordanElimination = function(leftMatrix, rightMatrix){

    var combined        = this.combineMatrices(leftMatrix, rightMatrix);
    var fwdIntegration  = this.forwardElimination(combined);
    //NOW, FINAL STEP IS BACKWARD SUBSTITUTION WHICH RETURNS THE TERMS NECESSARY FOR POLYNOMIAL REGRESSION
    return this.backwardSubstitution(fwdIntegration, [], fwdIntegration.length - 1, fwdIntegration[0].length - 2);
};

/**
 * returns the identity matrix for a matrix such that anyMatrix * identitymatrix = anyMatrix
 * This is useful for inverting a matrix
 * @param anyMatrix
 * @returns {*[]}
 */
Matrix.prototype.identityMatrix = function(anyMatrix){

    var rows = anyMatrix.length;
    var cols = anyMatrix[0].length;
    var identityMatrix = [[]];

    for (var i = 0; i < rows; i++) {
        for (var j = 0; j < cols; j++) {
            if (j == i) {
                identityMatrix[i][j] = 1;
            } else {
                identityMatrix[i][j] = 0;
            }
        }
    }
    return identityMatrix;
};


/**
 * calculates the product of 2 matrices
 * @param matrix1
 * @param matrix2
 * @returns {*}
 */
Matrix.prototype.matrixProduct = function(matrix1, matrix2){
    var numCols1 = matrix1[0].length;
    var numRows2 = matrix2.length;

    if(numCols1 != numRows2){
        return false;
    }

    var product = [[]];

    for (var rows = 0; rows < numRows2; rows++) {
        for (var cols = 0; cols < numCols1; cols++) {
            product[rows][cols] = this.doMultiplication(matrix1, matrix2, rows,
                cols, numCols1);
        }
    }
    return product;
};

/**
 * performs multiplication for an individual matrix cell
 * @param matrix1
 * @param matrix2
 * @param row
 * @param col
 * @param numCol
 * @returns {number}
 */
Matrix.prototype.doMultiplication = function(matrix1, matrix2, row, col, numCol){
    var counter = 0;
    var result = 0;
    while (counter < numCol) {
        result += matrix1[row][counter] * matrix2[counter][col];
        counter++;
    }
    return result;
};


/**
 * Multiplies a row of a matrix - 1 of the fundamental matrix operations
 * @param anyMatrix
 * @param rowNum
 * @param multiplier
 * @returns {*[]}
 */
Matrix.prototype.multiplyRow = function(anyMatrix, rowNum, multiplier){
    var rows = anyMatrix.length;
    var cols = anyMatrix[0].length;
    var mMatrix = [[]];

    for (var i = 0; i < rows; i++) {
        for (var j = 0; j < cols; j++) {
            if (i == rowNum) {
                mMatrix[i][j] = anyMatrix[i][j] * multiplier;
            } else {
                mMatrix[i][j] = anyMatrix[i][j];
            }
        }
    }

    return mMatrix;
};

/**
 * Simple data point object for use as a consistent data storage mechanism
 * @param x
 * @param y
 * @constructor
 */
function DataPoint(x,y){
    this.x = x;
    this.y = y;
}


/**
 * The constructor for a PolynomialRegression object an example of it's usage is below
 * @param theData
 * @param degrees
 * @constructor
 */
function PolynomialRegression(theData, degrees){


    //private object variables
    this.data        = theData;
    this.degree      = degrees;
    this.matrix      = new Matrix();
    this.leftMatrix  = [];
    this.rightMatrix = [];

    this.generateLeftMatrix();
    this.generateRightMatrix();
}

/**
 * Sums up all x coordinates raised to a power
 * @param anyData
 * @param power
 * @returns {number}
 */
PolynomialRegression.prototype.sumX = function(anyData, power){
    var sum = 0;
    for (var i = 0; i < anyData.length; i++) {
        sum += Math.pow(anyData[i].x, power);
    }
    return sum;
};


/**
 * sums up all x * y where x is raised to a power
 * @param anyData
 * @param power
 * @returns {number}
 */
PolynomialRegression.prototype.sumXTimesY = function(anyData, power){
    var sum = 0;
    for (var i = 0; i < anyData.length; i++) {
        sum += Math.pow(anyData[i].x, power) * anyData[i].y;
    }
    return sum;
};


/**
 * Sums up all Y's raised to a power
 * @param anyData
 * @param power
 * @returns {number}
 */
PolynomialRegression.prototype.sumY = function(anyData, power){
    var sum = 0;
    for (var i = 0; i < anyData.length; i++) {
        sum += Math.pow(anyData[i].y, power);
    }
    return sum;
};

/**
 * generate the left matrix
 */
PolynomialRegression.prototype.generateLeftMatrix = function(){
    for (var i = 0; i <= this.degree; i++) {
        this.leftMatrix.push([]);
        for (var j = 0; j <= this.degree; j++) {
            if (i === 0 && j === 0) {
                this.leftMatrix[i][j] = this.data.length;
            } else {
                this.leftMatrix[i][j] = this.sumX(this.data, (i + j));
            }
        }
    }
};

/**
 * generates the right hand matrix
 */
PolynomialRegression.prototype.generateRightMatrix = function(){
    for (var i = 0; i <= this.degree; i++) {
        if (i === 0) {
            this.rightMatrix[i] = this.sumY(this.data, 1);
        } else {
            this.rightMatrix[i] = this.sumXTimesY(this.data, i);
        }
    }
};


/**
 * gets the terms for a polynomial
 * @returns {*}
 */
PolynomialRegression.prototype.getTerms = function(){
    return this.matrix.gaussianJordanElimination(this.leftMatrix, this.rightMatrix);
};

/**
 * Predicts the Y value of a data set based on polynomial coefficients and the value of an independent variable
 * @param terms
 * @param x
 * @returns {number}
 */
PolynomialRegression.prototype.predictY = function(terms, x){

    var result = 0;
    for (var i = terms.length - 1; i >= 0; i--) {
        if (i == 0) {
            result += terms[i];
        } else {
            result += terms[i] * Math.pow(x, i);
        }
    }
    return result;
};

PolynomialRegression.runTest = function(degree, data){

  var poly = new PolynomialRegression(data, degree);
  var terms = poly.getTerms();

  actualData.forEach(function(data){
    regressionData.push({x: data.x, y: poly.predictY(terms, data.x)});
  });

  return {actual : actualData, regression : regressionData}
};

